body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primeblue: #1B7CEC;
  --secondaryblue: #c1deff;
  --primeblack: #333;
  --gray: #646464;
  --whiteshadow: #cce7ff;
  --white: #ffffff;
  --yellow: #ECE41B;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;

}

.center {
  margin: 0 auto;
  text-align: center;
}

.animation{
  transition-property: all;
  transition-duration: .7s;
  transition-timing-function: ease;
}

.white_font{
  color: #ffffff;
  color: var(--white);
}

.black_font{
  color: #333;
  color: var(--primeblack);
}

.gray_font{
  color: #646464;
}

.yellow_font{
  color: #ECE41B;
  color: var(--yellow);

}

.secondaryblue_font{
  color: #c1deff;
  color: var(--secondaryblue);

}

.blue_font{
  color: #1B7CEC;
  color: var(--primeblue);
}

.underline{
  text-decoration: underline;

}

.pointer{
  cursor: pointer;
}

.iconsocial {
  padding: 5px;
  margin: 5px;
  color: #ffffff;
  width: 30px;
  height: 30px;
  border: 1px solid #ffffff;
  border: 1px solid var(--white);
  border-radius: 19px;
}

.iconsocial:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.general_shadow{
  box-shadow: 1px 2px 6px 1px  #e0e0e0;
}

.white_bg{
 background-color: #ffffff;
 background-color: var(--white);
}

.flex{
  display: flex;
}

.App {
  /* font-family: Arial, Helvetica, sans-serif, sans-serif;; */
  text-align: center;
  max-width: 100%;

}

html, body{
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden; 
}

.btn_container {
  width: -webkit-fill-available;
  width: stretch;
}

.btn, .btn:visited, .btn:link{
  padding:7px 20px;
  font-size: 1.2rem;
  border-radius: 5px;
  cursor: pointer;
  min-width: 80px;

}

.btn:link {
  text-decoration: inherit;
}

.btn:visited {
  text-decoration: inherit;
}

.btn__primary{
  background-color: #1B7CEC;
  background-color: var(--primeblue);
  border:solid 0px #1B7CEC;
  border:solid 0px var(--primeblue);
  color : #ffffff;
  color : var(--white);
}

.btn__secondary{
  background-color: #ffffff;
  background-color: var(--white);  
  border:solid 1px #646464;  
  border:solid 1px var(--gray)
}

.btn__primary:hover, .btn__secondary:hover{
  box-shadow: 1px 2px 6px 1px  #e0e0e0;
}

.btn__primary:active, .btn__secondary:active{
  border:solid 1px #1B7CEC;
  border:solid 1px var(--primeblue);

}

.unbold {
  font-weight:normal;
}
​
.circle{
  border-radius: 100px;
}

.fit__content{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

}

.margin__top{
  margin-top: 10px;
}

#btn__checkout{
  width: -webkit-fill-available;
  width: fill;
}

@media (max-width:1160px) {
  .btn{
    padding:10px 20px;
    font-size: 1rem;
    border-radius: 5px;
  
  }

  .iconsocial {
    padding: 5px;
    margin: 5px;
    width: 15px;
    height: 15px;

    border-radius: 12px;
  }
}
.navbar {
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 auto;
    border-bottom: 1px solid var(--whiteshadow);
  }
  
  .navbar_container{
    width: 100%;
    margin: 0 70px;
    display: flex;
    flex-direction: row;
    justify-content:flex-end;
  
  }
  
  .nav_links {
    align-items: center;
    color: var(--primeblack);
    width: -webkit-fill-available;
    width: fill;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .nav_item{
    Padding: 40px;
    cursor: pointer;
    }
  
  .nav_item:link {
    text-decoration: inherit;
    color: inherit;
}

.nav_item:visited {
    text-decoration: inherit;
    color: inherit;
}
  
  .nav_logo{
    width: 150px;
  }
  
  .navbar it:hover {
    color: var(--primeblue);
  }

  .auth_icon{
    width: 30px;
  }
  
  @media (max-width:1160px) {
    .navbar_container{
      margin: 0 10px;
   
    }
  
    .nav_logo{
      width: 80px;
    }
  
    .nav_item{
      font-size: 1rem;
      Padding: 20px 10px;
    }
  }
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);
    
    display: flex;
    justify-content: center;
    align-items: center;

}

.popup__container{
    position: relative;
    text-align: left;
    top: 0px;
    padding: 32px;
    width: 100%;
    max-width: 1000px;
    background-color: #F9F9F9;
    border-radius: 10px;
    z-index: 5;
    display: flex;
    flex-wrap: wrap; 
    flex-direction: column;

}

.map__container{
    width: 100%;
}

.popup__container .save__button{
    bottom: 22px;
    right: 130px;
}

.popup__container .close__button{
    top: 22px;
    right: 32px;
}

.popup__container .save__button, .close__button{
    position: absolute;
    color: #1976D2;
    background-color: white;
    border: solid 0px;
    font-size: 1.2rem;

}

.popup__container .close__button:hover, .save__button:hover{
    text-decoration: underline;
    cursor: pointer;   
}

.popup__container h3{
    margin-bottom: 10px;
    align-items: flex-start;

}

.popup__model__container{
    display: flex;
    justify-content: space-between;
    max-width: 100%;

}

.map_container{
    height: '60vh'; 
    width: '50%'
}

.popup__is__charger__btn__container{
    /* display: flex;
    justify-content: center ; */
    /* max-width: 100%; */
}

.is__charger__btn{
    margin: 10px;
}

.popup__model__container div{
    /* background-color:    #fff; */


}

.popup__model{
    width: 300px;
    margin: 5px;
    padding: 15px;
    border-radius: 0px 0px 5px 5px;
    border: solid 1px #eee; 
    background-color: #fff;

}

.popup__model:hover, .popup__color:hover, .popup__year:hover{
    border: solid 1px #cbcbcb; 
}

.popup__model:active, popup__color:active, .popup__year:active  {
    border: solid 1px var(--primeblue); 
}
