}

.home_vehicles{
    background-color: #f9f9f9;
    width: 82%
}


.vehicles__container{
    position: relative;
    display: flex;
    flex-wrap: wrap; 
    flex-direction: row;
    justify-content: space-between;
 
}

.vehicle__container{
    position: relative;
    display: flex;
    flex-direction: column;
    align-self: stretch;
    max-width: 30%;
    min-width: 25%;
    background-color: white;
    justify-content:space-between;
    margin: 1% 1%;
    padding: 2%;
    border-radius: 10px;
    flex: 1 1 11em;
    box-shadow: 0 4px 7px 0px  rgba(224, 224, 224,0.2);
}

.charger__container{
    max-width: 50%;

}


.vehicle__content> .vehicle__image {
    height: 200px;
    max-width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    object-fit: cover;
    
}



.vehicle__container:focus,
.vehicle__container:hover {
    box-shadow: 0 4px 7px 0px  rgba(224, 224, 224,1);
}

.partners_container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.partners_image{
    border: solid 1px #eee; 
    border-radius: 10px;
    width: 300px;
    margin: 30px;

}
.footer_container{
    flex-direction:column;
    flex-wrap: wrap; 

}
.footer_content{
    display: flex;
    flex-wrap: wrap; 
    justify-content: space-around;
}

.footer_element{
    width: 25%;
}

